import React from "react";
import "./news.css";

const News = (props) => {
  return (
    <div className="App">
      {props.news.map(({ author, title, url, urlToImage, content }, i) => (
        <div key={i}>
          <div>
            <h1>{title}</h1>
          </div>
          <div>{author}</div>
          <div className="item">
            <p>
              <img src={urlToImage} alt="" />
            </p>
          </div>
          <div>{content}</div>
          <div>
            <form action={url}>
              {" "}
              <button type="submit">Подробнее</button>{" "}
            </form>
          </div>
        </div>
      ))}
    </div>
  );
};

export default News;
